"use client";
import { useState, useEffect } from "react";
import { useRouter } from "next/navigation";
import api from "@/lib/api";

export default function ClientesPage() {
  const [clientes, setClientes] = useState([]);
  const [loading, setLoading] = useState(true);
  const [modalOpen, setModalOpen] = useState(false);
  const [clienteSelecionado, setClienteSelecionado] = useState(null);
  const router = useRouter();

  useEffect(() => {
    async function fetchClientes() {
      try {
        const response = await api.get("/clientes");
        setClientes(response.data);
      } catch (error) {
        console.error("Erro ao buscar clientes:", error);
      } finally {
        setLoading(false);
      }
    }
    fetchClientes();
  }, []);

  const handleDeleteClick = (cliente) => {
    setClienteSelecionado(cliente);
    setModalOpen(true);
  };

  const confirmDelete = async () => {
    if (!clienteSelecionado) return;
    try {
      await api.delete(`/clientes/${clienteSelecionado.codigo}`);
      setClientes(clientes.filter((c) => c.codigo !== clienteSelecionado.codigo));
      setModalOpen(false);
      setClienteSelecionado(null);
    } catch (error) {
      console.error("Erro ao excluir cliente:", error);
    }
  };

  return (
    <div className="max-w-7xl mx-auto p-6">
      {/* Cabe√ßalho melhorado */}
      <div className="bg-gradient-to-r from-blue-600 to-blue-800 text-white p-6 rounded-lg shadow-lg flex justify-between items-center">
        <h1 className="text-4xl font-extrabold tracking-wide">üìã Clientes</h1>
        <button
          onClick={() => router.push("/clientes/novo")}
          className="bg-green-500 text-white px-6 py-3 text-lg rounded-lg hover:bg-green-600 transition-all shadow-md"
        >
          + Criar Cliente
        </button>
      </div>

      {loading ? (
        <p className="text-gray-500 mt-6 text-center">Carregando...</p>
      ) : (
        <div className="bg-white shadow-md rounded-lg overflow-hidden mt-6">
          <table className="w-full border-collapse">
            <thead className="sticky top-0 bg-gray-800 text-white">
              <tr>
                <th className="p-4 text-left">C√≥digo</th>
                <th className="p-4 text-left">Nome</th>
                <th className="p-4 text-left">Fantasia</th>
                <th className="p-4 text-left">Documento</th>
                <th className="p-4 text-left">Endere√ßo</th>
                <th className="p-4 text-center">Editar</th>
                <th className="p-4 text-center">Excluir</th>
              </tr>
            </thead>
            <tbody>
              {clientes.map((cliente, index) => (
                <tr key={cliente.codigo} className={`border-t ${index % 2 === 0 ? "bg-gray-100" : "bg-white"}`}>
                  <td className="p-4">{cliente.codigo}</td>
                  <td className="p-4">{cliente.nome}</td>
                  <td className="p-4">{cliente.fantasia || "N/A"}</td>
                  <td className="p-4">{cliente.documento}</td>
                  <td className="p-4">{cliente.endereco}</td>
                  <td className="p-4 text-center">
                    <button
                      onClick={() => router.push(`/clientes/editar/${cliente.codigo}`)}
                      className="bg-blue-500 text-white px-3 py-1 rounded hover:bg-blue-600 transition-all"
                    >
                      ‚úèÔ∏è Editar
                    </button>
                  </td>
                  <td className="p-4 text-center">
                    <button
                      onClick={() => handleDeleteClick(cliente)}
                      className="bg-red-500 text-white px-3 py-1 rounded hover:bg-red-600 transition-all"
                    >
                      üóëÔ∏è Excluir
                    </button>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      )}

      {/* Modal de Confirma√ß√£o */}
      {modalOpen && (
        <div className="fixed inset-0 flex items-center justify-center bg-black bg-opacity-50">
          <div className="bg-white p-6 rounded-lg shadow-lg text-center">
            <p className="text-lg mb-4">
              Tem certeza que deseja excluir <b>{clienteSelecionado?.nome}</b>?
            </p>
            <div className="flex justify-center gap-4">
              <button
                onClick={confirmDelete}
                className="bg-red-500 text-white px-4 py-2 rounded-lg hover:bg-red-600 transition-all"
              >
                Sim, excluir
              </button>
              <button
                onClick={() => setModalOpen(false)}
                className="bg-gray-400 text-white px-4 py-2 rounded-lg hover:bg-gray-500 transition-all"
              >
                Cancelar
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}
