import axios from "axios";
import Cookies from "js-cookie"; // Importando a lib para lidar com cookies

// Fun√ß√£o para obter o token do cookie
const getToken = () => Cookies.get("token");

// Criando inst√¢ncia do Axios
const api = axios.create({
  baseURL: "https://localhost:7203/api",
  headers: {
    "Content-Type": "application/json",
  },
});

// Interceptor para adicionar o token JWT em todas as requisi√ß√µes
api.interceptors.request.use(
  (config) => {
    const token = getToken();
    if (token) {
      config.headers.Authorization = `Bearer ${token}`;
    }
    return config;
  },
  (error) => Promise.reject(error)
);

export default api;
